@import "src/global";

.intro {
  background-color: lightblue;
  display: flex;

  @include mobile {
    flex-direction: column;
    align-items: center;
  }

  .left {
    flex: 0.5;
    .imgContainer {
      width: 700px;
      height: 700px;
      background-color: crimson;
      border-radius: 50%;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      float: right;

      @include mobile {
        align-items: center;
      }
      img {
        object-fit: contain;
        width: 100%;
        @include mobile {
          height: 50%;
        }
      }
    }
  }
  .right {
    flex: 0.5;
    .wrapper {
      width: 100%;
      height: 100%;
      display: flex;
      padding-left: 50px;
      flex-direction: column;
      justify-content: center;

      @include mobile {
        padding-left: 0;
        align-items: center;
      }

      h1 {
        font-size: 60px;
        margin: 10px 0;
        @include mobile {
          font-size: 36px;
        }
      }
      h2 {
        font-size: 35px;
      }
      h3 {
        font-size: 35px;
        span {
          font-style: inherit;
          color: crimson;
        }
        .ityped-cursor {
          animation: blink 1s infinite;
        }
        @keyframes blink {
          50% {
            opacity: 1;
          }
          100%{
            opacity: 0;
          }
        }
      }
      a {
        position: absolute;
        bottom: 10px;
        left: 50%;
        img {
          width: 30px;
          animation: arrowBlink 2s infinite;
        }
        @keyframes arrowBlink {
          100%{
            opacity: 0;
          }
        }
      }
    }
  }
}
